          return "连接失败"
        }
        if (this.logstatus == "disconnected"){
          return  "连接断开"
        }
        if (this.logstatus=="checking"){
          return "连接中..."
        }else if (this.logstatus=="connected"){
          return "连接成功"
        }else{
          return "准备连接"
        }  
      }
    },
    methods: {
      createPeerConnection(){
          if(!this.pc){
            console.log("creat connecte")
            this.pc = new RTCPeerConnection(this.pcConfig)
            var pc=this.pc
            pc.oniceconnectionstatechange = () => {this.logstatus=pc.iceConnectionState;}
            var that = this
            pc.ontrack = function (event) {
              var transceivers = that.pc.getTransceivers()
              document.getElementById('remoteVideos').innerHTML=""
              for (var i=0;i<transceivers.length;i++){
                var el = document.createElement(event.track.kind)
                var track = transceivers[i].receiver.track
                var stream = new MediaStream()
                stream.addTrack(track)
                el.setAttribute("width",200)
                el.setAttribute("height",200)
                el.srcObject = null
                el.srcObject = stream
                el.autoplay = true
                el.controls = true
                document.getElementById('remoteVideos').appendChild(el)
              }
            }
            pc.onnegotiationneeded = () => {
              console.log("onnegotiationneeded")
              pc.createOffer()
              .then(d => pc.setLocalDescription(d)).then(()=>{
                if (this.firstIcecandidate){
                // if (e.candidate === null&&this.firstIcecandidate) {
                  // console.log("onicecandidate***")
                  this.firstIcecandidate = false
                  var offerBase = btoa(JSON.stringify(pc.localDescription))
                  console.log("offerBase:",offerBase)
                  //建一个配置
                  var clientId =this.clientId
                  var topic =this.topic
                  var bodydata = 'id=' +clientId+ '&topic='+topic+'&type='+'4'+'&offer='+offerBase;
                  var  options = {
                    method: 'POST',//post请求
                    headers: {
                      'Accept': 'application/json',
                      // 'Content-Type': 'application/json'
                      'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
                    },
                    body:bodydata
                  }
                   console.log("onicecandidate1")
                  //新建一个fetch请求
                  fetch(this.apiUrl,options).then((res)=>{
                    console.log("onicecandidate2")
                      if(res.ok){//如果取数据成功
                        console.log(res)
                        res.json().then((data)=>{
                          var sd =data.message.split(":")[1]
                          console.log('answerSd',sd)
                          that.answerSd = sd
                          // that.startSession()
                        })
                      }else{
                        console.log(res.status);
                      }
                  }).catch((res)=>{
                     console.log(res.status);
                  })
                }
              })

            }
            pc.onicecandidate = () =>{
              console.log("onicecandidate")
              // if (this.firstIcecandidate){
              // // if (e.candidate === null&&this.firstIcecandidate) {
              //   console.log("onicecandidate***")
              //   this.firstIcecandidate = false
              //   var offerBase = btoa(JSON.stringify(pc.localDescription))
              //   console.log("offerBase:",offerBase)
              //   //建一个配置
              //   var clientId =this.clientId
              //   var topic =this.topic
              //   var bodydata = 'id=' +clientId+ '&topic='+topic+'&type='+'4'+'&offer='+offerBase;
              //   var  options = {
              //     method: 'POST',//post请求
              //     headers: {
              //       'Accept': 'application/json',
              //       // 'Content-Type': 'application/json'
              //       'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
              //     },
              //     body:bodydata
              //   }
              //    console.log("onicecandidate1")
              //   //新建一个fetch请求
              //   fetch(this.apiUrl,options).then((res)=>{
              //     console.log("onicecandidate2")
              //       if(res.ok){//如果取数据成功
              //         console.log(res)
              //         res.json().then((data)=>{
              //           var sd =data.message.split(":")[1]
              //           console.log('answerSd',sd)
              //           that.answerSd = sd
              //         })
              //       }else{
              //         console.log(res.status);
              //       }
              //   }).catch((res)=>{
              //      console.log(res.status);
              //   })
              // }
            }
            this.ctlChannel= pc.createDataChannel('control')
            var ctlChannel=this.ctlChannel
            // pc.createOffer()
            //   .then(d => pc.setLocalDescription(d))
            ctlChannel.onopen=()=>{
              console.log("ctlChannel is on")
            }
            ctlChannel.onmessage=(event)=>{
              console.log(event)
            }
            pc.ondatachannel=(event)=>{
              var receiveBuffer=[]
              var filesize 
              var receivedSize = 0
              var channel = event.channel
              this.up_percent= 0
              channel.onmessage=(event)=>{
                if (channel.label == "sdp") {
                  if(event.data=="ready"){
                    // if(this.pc.getTransceivers().length <= this.activeVideos) {
                    console.log("b0",pc.getTransceivers().length)
                    this.pc.addTransceiver('video')
                    console.log("b1",pc.getTransceivers().length)
                    console.log("b2",pc.getTransceivers().length)
                    this.pc.createOffer().then((offer)=>{
                      // if(this.latesOffer != offer ){
                        console.log("created:",offer)
                        console.log(this.pc)
                        this.pc.setLocalDescription(offer)
                        // this.latesOffer = offer 
                      // }
                      console.log(offer)
                      channel.send(btoa(JSON.stringify(offer)))
                      console.log("b3",pc.getTransceivers().length)
                    });
                    return
                  }
                  console.log("b4",pc.getTransceivers().length)
                  var sdp = new RTCSessionDescription(JSON.parse(atob(event.data)))
                  console.log(sdp)
                  this.pc.setRemoteDescription(sdp)
                  console.log("b5",pc.getTransceivers().length)
                } 
                if (channel.label == "shot"){
                  console.log("shot")
                  if(typeof(event.data)=="string"){
                      var meta = JSON.parse(atob(event.data))
                      console.log(atob(event.data))
                      filesize=meta.filesize
                  }else{
                    receiveBuffer.push(event.data);
                    receivedSize += event.data.byteLength;
                    this.up_percent = (receivedSize/filesize*100).toFixed(0)*1
                    if (receivedSize === filesize) {
                      const received = new Blob(receiveBuffer);
                      receiveBuffer = [];
                      this.url = URL.createObjectURL(received);
                      this.srcList.push(this.url)
                    }
                  }

                }

              }
            }

          }else {
            console.warning('the pc have be created!');
          }
          return; 
