        start(){
          this.pc=null
          this.createPeerConnection()
        },
        startSession(){
          var  sd = this.answerSd
          if (sd === '') {
            this.logstatus = "lost"
            return alert("连接失败")
          }
          try {
            this.pc.setRemoteDescription(new RTCSessionDescription(JSON.parse(atob(sd))))
          } catch (e) {
            alert(e)
          }
        },
        job(e){
            var ms = null
            var timeflag = new Date().getTime()
            switch (e){
              case "shot":
                this.url=""
                var picUrl = this.shotUrl
        
                if(this.ctlChannel != null&&this.ctlChannel.readyState=="open"){
                  if (picUrl == ""){
                      picUrl = "screen"
                  }
                  // else{
                  //   // picUrl="ffmpeg |_|-i|_|"+picUrl+"|_|-vframes|_|1|_|-y|_|-f|_|image2"
                  //   picUrl=picUrl
                  // }
                  
                  ms = {
                    id:this.clientId,
                    topic:this.topic,
                    type:"3",
                    message:{
                      shot:picUrl,
                      time:timeflag,
                    }
                  }
                  ms = btoa(JSON.stringify(ms))
                  this.ctlChannel.send(ms)
                  
                }else{
                  console.log("shot grpc")
                  this.sendCmd({category:3,cmd:picUrl,},(res)=>{
                    res.json().then((data)=>{
                        data =data.message.split(":")[1]
                        console.log(data)
                        // alert(data)
                        this.$message(data);
                      })
                  })
                }

                break
                // Msg: path,Loop: int32(loop),Fast:1,Pause:false,Volume:0,Type:0,Timestamp:timestamp
              case "playsound":
                var soundUrl = this.soundUrl
                if(this.ctlChannel != null&&this.ctlChannel.readyState=="open"){
                    if (soundUrl == ""){
                      alert("mp3地址不能为空")
                    }else{
                      ms = {
                        id:this.clientId,
                        topic:this.topic,
                        type:"1",
                        message:{
                          loop:1,
                          path:this.soundUrl,
                          time:timeflag,
                        }
                      }
                    }
                    ms = btoa(JSON.stringify(ms))
                    this.ctlChannel.send(ms)
                }else{
                  this.sendCmd({category:1, times:1,path:soundUrl,},(res)=>{
                    res.json().then((data)=>{
                        data =data.message.split(":")[1]
                        console.log(data)
                      })
                  })
                }
                break
              case "live":
                if (this.videoUrl == ""){
                  alert("视频地址不能为空")
                  return
                }
                ms = {
                  url:this.videoUrl,
                  type:"5",
                }
                ms = btoa(JSON.stringify(ms))
                // this.totalVodeos = this.totalVodeos+1
                // console.log("total",this.totalVodeos)
                this.ctlChannel.send(ms)
                break
                
            }
            
          // }
          
        },
        shotByArea(index){
          console.log("shotByArea")
          this.shotUrl=""
          this.areaTag=this.areainfos[index]
          var bodydata = 'code=' +this.areas[index]
          //新建一个fetch请求
          var that =  this
          var  options = {
            method: 'POST',//post请求
            headers: {
              'Accept': 'application/json',
              // 'Content-Type': 'application/json'
              'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
            },
            body:bodydata
          }
          fetch("http://118.122.120.57:18080/api/gis/video/live-url",options).then((res)=>{
              if(res.ok){//如果取数据成功
                //console.log(res)
                res.json().then((data)=>{
                  if(data.code==90010){
                    console.log(data.data.list)
                    that.shotUrl=data.data.list
                    that.job("shot")
                  }
                })
              }else{
                console.log(res.status);
              }
          }).catch((res)=>{
             console.log(res.status);
          })
        },
        startLocalStream(){
            navigator.getUserMedia = navigator.getUserMedia ||
                                     navigator.webkitGetUserMedia ||
                                     navigator.mozGetUserMedia;
            var that = this                        
            if (navigator.getUserMedia) {
               navigator.getUserMedia({ audio: true, video: { width: 400, height: 400 } },
                  function(stream) {
                     var video = document.querySelector('video');
                     // video.src = window.URL.createObjectURL(stream);
                     video.srcObject=stream
                     that.localStream=stream
                     console.log(stream)
                     video.onloadedmetadata = function() {
                       video.play();
                     };
                  },
                  function(err) {
                     console.log("The following error occurred: " + err.name);
                  }
               );
            } else {
               console.log("getUserMedia not supported");
            }
        },

        sendCmd(option,fb){
          if (this.logstatus != "connected"){
            //建一个配置
              var clientId =this.clientId
              var topic =this.topic
              if (clientId ==""||topic == ""){
                alert("id和topic不能为空")
              }
              var bodydata = 'id=' +clientId+ '&topic='+topic+'&type='+option.category+'&times='+option.times+'&path='+option.path+'&cmd='+option.cmd;

              console.log("bodydata",bodydata)
              var  options = {
                method: 'POST',//post请求
                headers: {
                  'Accept': 'application/json',
                  // 'Content-Type': 'application/json'
                  'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
                },
                body:bodydata
              }
              //新建一个fetch请求
              fetch(this.apiUrl,options).then((res)=>{
                  if(res.ok){//如果取数据成功
                    fb(res)
                  }else{
                   console.log(res.status);
                  }
              }).catch((res)=>{
                 console.log(res.status);
            })
          }
   
        }


